<template>
      <v-list-item-content>
              <v-list-item-title>EHS </v-list-item-title>
              <v-card class="mx-auto" >
                    <v-card-text>
                    <p class="display-1 text--primary">
                       {{ ehs.score }} 
                       <v-chip class="ma-2" color="red" text-color="white" v-if="ehs.score == 0"> </v-chip>
                       <v-chip class="ma-2" color="orange" text-color="white" v-if="ehs.score == 1">  </v-chip>
                       <v-chip class="ma-2" color="light-green" text-color="white" v-if="ehs.score == 2">  </v-chip>
                       <v-chip class="ma-2" color="green" text-color="white" v-if="ehs.score == 3">  </v-chip>
                    </p>
                    <p>{{ ehs.time | time }}</p>
                    <div class="text--primary">
                        1. <br>
                        <v-chip class="ma-2" :class="{'primary primary--text':ehs.score == 0}" outlined pill >  </v-chip>
                        <br>
                        <v-chip class="ma-2" :class="{'primary primary--text':ehs.score == 1}" outlined pill >  </v-chip>
                        <br>
                        <v-chip class="ma-2" :class="{'primary primary--text':ehs.score == 2}" outlined pill >  </v-chip>
                        <br>
                        <v-chip class="ma-2" :class="{'primary primary--text':ehs.score == 3}" outlined pill >  </v-chip>
                    </div>
                    </v-card-text>
                    <v-card-actions>
                    </v-card-actions>
                </v-card>
            </v-list-item-content>
</template>

<script>
export default {
    name:'EHS',
    props:{
        ehs:{
            score: Number,
            time: Number,
            user_id: Number
        }
    },
  data() {
    return {};
  },
  methods: {}
};
</script>

