<template>
      <v-list-item-content>
              <v-list-item-title></v-list-item-title>
              <v-card class="mx-auto" >
                    <v-card-text>
                    <div class="text--primary">
                        <span class="tit" >1.  </span> 
                        <br>
                        <v-chip class="ma-2 primary primary--text" outlined pill > {{ file.name }} </v-chip>
                        <br>
                        <span class="tit" >2.  </span> 
                        <br>
                        <v-chip class="ma-2 primary primary--text" outlined pill > {{ file.birthday | time }} </v-chip>
                        <br>
                        <span class="tit" >3.  </span> 
                        <br>
                        <v-chip class="ma-2 primary primary--text" outlined pill > {{ file.height }}cm </v-chip>
                        <br>
                        <span class="tit" >4.  </span> 
                        <br>
                        <v-chip class="ma-2 primary primary--text" outlined pill > {{ file.weight }}kg </v-chip>
                        <br>
                        <br>
                        <hr>
                        <br>
                        <span class="tit" >5.  </span> 
                        <br>
                        <v-chip class="ma-2 primary primary--text" outlined pill > {{ file.marriage==0?'':"" }} </v-chip>
                        <br>
                        <span class="tit" >6.  </span> 
                        <br>
                        <v-chip class="ma-2 primary primary--text" outlined pill > {{ file.breeding==0?"":'' }} </v-chip>
                        <br>
                        <span class="tit" >7.  </span> 
                        <br>
                        <v-chip class="ma-2 primary primary--text" outlined pill > {{ file.sexual_life==0?'':'' }} </v-chip>
                        <br>
                        <span class="tit" >8.  </span> 
                        <br>
                        <v-chip class="ma-2 primary primary--text" outlined pill > {{ file.morning_wood==0?'':'' }} </v-chip>
                        <br>
                        <span class="tit" >9.  </span> 
                        <br>
                        <v-chip class="ma-2 primary primary--text" outlined pill > {{ file.sexual_stimulus==0?'':'' }} </v-chip>
                        <br>
                        <span class="tit" >10.  </span> 
                        <br>
                        <v-chip class="ma-2 primary primary--text" outlined pill > {{ file.duration }} </v-chip>
                        <br>
                        <span class="tit" >11.   </span> 
                        <br>
                        <v-chip class="ma-2 primary primary--text" outlined pill v-for="item in file.disease" :key="item" > {{ item }} </v-chip>
                        <br>
                        <span class="tit" >12.   </span> 
                        <br>
                        <v-chip class="ma-2 primary primary--text" outlined pill v-for="item in file.trauma" :key="item" > {{ item }} </v-chip>
                        <v-chip class="ma-2" outlined pill v-if="file.trauma.length == 0" >  </v-chip>
                        <br>
                        <span class="tit" >13.   </span> 
                        <br>
                        <v-chip class="ma-2 primary primary--text" outlined pill >
                            <span v-if="file.smoke == 0"></span>
                            <span v-else-if="file.smoke == 1"></span>
                            <span v-else-if="file.smoke == 2"></span>
                            <span v-else-if="file.smoke == 3"></span>
                        </v-chip>
                        <br>
                        <span class="tit" >14.   </span> 
                        <br>
                        <v-chip class="ma-2 primary primary--text" outlined pill >
                            <span v-if="file.drink == 0"></span>
                            <span v-else-if="file.drink == 1"></span>
                            <span v-else-if="file.drink == 2"></span>
                            <span v-else-if="file.drink == 3"></span>
                        </v-chip>
                        <br>
                        <span class="tit" >15.   </span> 
                        <br>
                        <v-chip class="ma-2 primary primary--text" outlined pill v-for="item in file.medicine" :key="item"  > {{ item }} </v-chip>
                        <v-chip class="ma-2" outlined pill v-if="file.medicine.length == 0" >  </v-chip>
                        <br>
                    </div>
                    </v-card-text>
                    <v-card-actions>
                    </v-card-actions>
                </v-card>
            </v-list-item-content>
</template>

<script>
export default {
    name:'EHS',
    props:{
        file:{}
    },
  data() {
    return {};
  },
  methods: {}
};
</script>

<style lang="scss" scoped>
.tit{
    font-size: 16px;
    color: #000;
}
.v-card__text{
    padding-left: 50px;
}
.v-list-item__title{
    padding-left: 50px;
}
</style>