<template>
  <v-list-item-content>
              <v-list-item-title>IIEF-5 </v-list-item-title>
              <v-card class="mx-auto" >
                    <v-card-text>
                    <p class="display-1 text--primary">
                       {{ iief5Number }} 
                       <v-chip class="ma-2" color="red" text-color="white" v-if="iief5Number <= 7"> </v-chip>
                       <v-chip class="ma-2" color="orange" text-color="white" v-if="iief5Number > 7 && iief5Number <= 11"> </v-chip>
                       <v-chip class="ma-2" color="light-green" text-color="white" v-if="iief5Number > 11 && iief5Number <= 21"> </v-chip>
                       <v-chip class="ma-2" color="green" text-color="white" v-if="iief5Number > 21">  </v-chip>
                    </p>
                    <p>{{ iief5.create_time | time }}</p>
                    <div class="text--primary">
                        1. <br>
                        <v-chip class="ma-2" :class="{'primary primary--text':iief5.scores[0] == 0}" outlined pill >  </v-chip>
                        <v-chip class="ma-2" :class="{'primary primary--text':iief5.scores[0] == 1}" outlined pill >  </v-chip>
                        <v-chip class="ma-2" :class="{'primary primary--text':iief5.scores[0] == 2}" outlined pill >  </v-chip>
                        <v-chip class="ma-2" :class="{'primary primary--text':iief5.scores[0] == 3}" outlined pill >  </v-chip>
                        <v-chip class="ma-2" :class="{'primary primary--text':iief5.scores[0] == 4}" outlined pill >  </v-chip>
                        <v-chip class="ma-2" :class="{'primary primary--text':iief5.scores[0] == 5}" outlined pill >  </v-chip>
                    </div>
                    <br>
                    <div class="text--primary">
                        2. <br>
                        <v-chip class="ma-2" :class="{'primary primary--text':iief5.scores[1] == 0}" outlined pill >  </v-chip>
                        <v-chip class="ma-2" :class="{'primary primary--text':iief5.scores[1] == 1}" outlined pill > / </v-chip>
                        <v-chip class="ma-2" :class="{'primary primary--text':iief5.scores[1] == 2}" outlined pill >  </v-chip>
                        <v-chip class="ma-2" :class="{'primary primary--text':iief5.scores[1] == 3}" outlined pill >  </v-chip>
                        <v-chip class="ma-2" :class="{'primary primary--text':iief5.scores[1] == 4}" outlined pill >  </v-chip>
                        <v-chip class="ma-2" :class="{'primary primary--text':iief5.scores[1] == 5}" outlined pill > / </v-chip>
