                        <v-chip class="ma-2" :class="{'primary primary--text':iief5.scores[2] == 3}" outlined pill >  </v-chip>
                        <v-chip class="ma-2" :class="{'primary primary--text':iief5.scores[2] == 4}" outlined pill >  </v-chip>
                        <v-chip class="ma-2" :class="{'primary primary--text':iief5.scores[2] == 5}" outlined pill > / </v-chip>
                    </div>
