                        <v-chip class="ma-2" :class="{'primary primary--text':iief5.scores[3] == 2}" outlined pill >  </v-chip>
                        <v-chip class="ma-2" :class="{'primary primary--text':iief5.scores[3] == 3}" outlined pill >  </v-chip>
                        <v-chip class="ma-2" :class="{'primary primary--text':iief5.scores[3] == 4}" outlined pill >  </v-chip>
                        <v-chip class="ma-2" :class="{'primary primary--text':iief5.scores[3] == 5}" outlined pill >  </v-chip>
                    </div>
