                    <div class="text--primary">
                        5. <br>
                        <v-chip class="ma-2" :class="{'primary primary--text':iief5.scores[4] == 0}" outlined pill >  </v-chip>
                        <v-chip class="ma-2" :class="{'primary primary--text':iief5.scores[4] == 1}" outlined pill > / </v-chip>
                        <v-chip class="ma-2" :class="{'primary primary--text':iief5.scores[4] == 2}" outlined pill >  </v-chip>
                        <v-chip class="ma-2" :class="{'primary primary--text':iief5.scores[4] == 3}" outlined pill >  </v-chip>
                        <v-chip class="ma-2" :class="{'primary primary--text':iief5.scores[4] == 4}" outlined pill >  </v-chip>
                        <v-chip class="ma-2" :class="{'primary primary--text':iief5.scores[4] == 5}" outlined pill > / </v-chip>
