                    <!-- <v-btn text color="deep-purple accent-4" >
                        Learn More
                    </v-btn> -->
                    </v-card-actions>
                </v-card>
            </v-list-item-content>
</template>

<script>
export default {
    name:'IIEF5',
    props:{
        iief5:{
            create_time:Number,
            scores:Array
        },
        iief5Number: Number
    },
  data() {
    return {
      dialog: false,
      data: {}
    };
  },
  methods: {
    info(state, data) {
      this.dialog = state;
      this.data = data;
    },
  }
};
</script>

